import React from 'react';
const Login = () => {
  return (
    <div id='login-container'  className='h-screen w-screen flex flex-col items-center justify-center'>
      <div className='flex flex-col items-center justify-center bg-white px-5 rounded-lg'>
      <img className='w-28 my-6' src='/images/logo.png' alt="" />
<form className='flex flex-col space-y-1'>
    <input className='border-2 rounded-sm px-2 border-yellow-400 py-1' type="text" placeholder='email'/>
    <input className='border-2 rounded-sm px-2 border-yellow-400 py-1' type="password" name="" placeholder='Password'/>
    <button className='px-4 rounded-sm bg-yellow-400 text-white' type="submit">Login</button>
    <p className='text-white text-sm'>Create new account? <a href="/" className='text-yellow-400'>Signup</a></p>
</form>
</div>
    </div>
  )
}

export default Login