import React from 'react'
import Footer from '../components/Footer'
import Navbar from '../components/Navbar'
import NewsLetter from '../components/NewsLetter'

const Cart = () => {
    return (
        <>
        <Navbar/>
        <div className=''>
            <h1 className='text-center text-3xl primary-color my-3'>Your Bag</h1>
          <CartItem/>
          <CartItem/>
          <CartItem/>
          <NewsLetter/>
          <Footer/>
        </div>
        </>
    )
}

const CartItem=()=>{

    return(
        <div className='flex items-center border xs:px-4 px-2 xs:mx-4 mx-2'>
        <img className='w-36 xs:w-52' src='/images/p1.jpg' alt="product_image" />
        <div>
        <h1 className='xs:text-lg text-sm primary-color'>Shirt made with pure cotton</h1>
        <p className='text-xs xs:text-sm'>Lorem ipsum dolor sit amet consectetur adipisicing elit. Soluta, reprehenderit.</p>
        <ul className='flex space-x-2'>
            <li className='flex items-center space-x-1 xs:space-x-2 text-sm xs:text-base'>Color: <p> </p> <div className='bg-yellow-400 rounded-lg w-4 h-4'></div><div className='bg-black rounded-lg w-4 h-4'></div><div className='bg-red-600 rounded-lg w-4 h-4'></div></li>
            <li className='text-sm xs:text-base'>Size: <select className='border border-yellow-400' name="" id=""><option value="">sm</option>
            <option value="">md</option>
            <option value="">lg</option></select></li>
        </ul>
        <div>
            <button className=' xs:text-4xl text-2xl px-3'>-</button><span className='border-2 border-yellow-400 px-2 rounded'>1</span><button className=' xs:text-4xl text-2xl px-3'>+</button>
        </div>
        <p className='py-3'><strong>Price: </strong>$19</p>
         </div>
    </div>
    )
}

export default Cart