import { SearchSharp } from '@material-ui/icons'
import React, { useState } from 'react';

const Carousel = () => {
    let sourceName = ['/images/image2.jpg', '/images/image1.jpg', '/images/image0.jpg']
    const [number, setnumber] = useState(0)
    const [picIndex, setPicIndex] = useState(sourceName[number])
    const slideNext = () => {
        if (number < 2) {
            setnumber(number + 1)
            setPicIndex(sourceName[number])
        } else {
            setnumber(0)
            setPicIndex(sourceName[number])
        }
    }
    return (
        <div id="indicators-carousel" className="relative" >
            <div className='absolute z-30 bg-[#585755a6] secondary-color flex flex-col justify-center items-center w-3/4 left-[12.5%] top-10 sm:top-20 xs:top-16 md:top-1/3 rounded-lg xs:p-2'
                data-aos="flip-up"
                data-aos-offset="200"
                data-aos-delay="50"
                data-aos-duration="1000"
                data-aos-easing="ease-in-out">

                <h1 className='text-2xl'>Welcome to arFashion</h1>
                <p className=' text-xs md:text-sm text-center'>Lorem ipsum, dolor sit amet consectetur adipisicing elit. Veniam inventore corrupti perferendis illo, dolorem, mollitia dolor, in rerum reprehenderit praesentium debitis minima animi libero numquam illum ullam autem hic! Voluptatem.</p>
                <div>
                    <div className='border-2 border-yellow-500 rounded-md my-1'>
                        <input className='bg-[#62615f30] px-1 outline-none  sm:w-auto' type="search" placeholder='Search the style...' /><SearchSharp className='cursor-pointer' />
                    </div>
                </div>
            </div>
            {/*  Carousel wrapper  */}
            <div className="relative h-56 overflow-hidden rounded-lg md:h-[65vh]">
                {/*  Item 1  */}
                <div className="duration-700 ease-in-out absolute inset-0 transition-all transform translate-x-0 z-20" data-carousel-item="active">
                    <img src={picIndex} className="absolute block w-full -translate-x-1/2 -translate-y-1/2 top-1/2 left-1/2" alt="..." />
                </div>

            </div>
            {/*  Slider controls  */}
            <button type="button" className="absolute top-0 left-0 z-30 flex items-center justify-center h-full px-3 xs:px-4 cursor-pointer group focus:outline-none" data-carousel-prev="">
                <span className="inline-flex items-center justify-center w-8 h-8 rounded-full sm:w-10 sm:h-10 bg-white/30 dark:bg-gray-800/30 group-hover:bg-white/50 dark:group-hover:bg-gray-800/60 group-focus:ring-4 group-focus:ring-white dark:group-focus:ring-gray-800/70 group-focus:outline-none">
                    <svg aria-hidden="true" className="w-5 h-5 text-white sm:w-6 sm:h-6 dark:text-gray-800" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path className='text-black' strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M15 19l-7-7 7-7" ></path></svg>
                    <span className="sr-only">Previous</span>
                </span>
            </button>
            <button type="button" className="absolute top-0 right-0 z-30 flex items-center justify-center h-full px-3 xs:px-4 cursor-pointer group focus:outline-none" data-carousel-next="" onClick={slideNext}>
                <span className="inline-flex items-center justify-center w-8 h-8 rounded-full sm:w-10 sm:h-10 bg-white/30 dark:bg-gray-800/30 group-hover:bg-white/50 dark:group-hover:bg-gray-800/60 group-focus:ring-4 group-focus:ring-white dark:group-focus:ring-gray-800/70 group-focus:outline-none">
                    <svg aria-hidden="true" className="w-5 h-5 text-white sm:w-6 sm:h-6 dark:text-gray-800" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path className='text-black' strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M9 5l7 7-7 7"></path></svg>
                    <span className="sr-only">Next</span>
                </span>
            </button>
        </div>

    )
}

export default Carousel