import { StarOutlineOutlined } from '@material-ui/icons'
import React from 'react'

const Product = ({source}) => {
    return (
        <div className='w-80 border overflow-hidden'>
            <a href="/">
            <img className='w-80 hover:scale-110 transition-all overflow-hidden' src={source} alt="" />
            <div className='px-2'>

                <h3 className='text-sm relative z-10'>The Vintage Clothing gym printed premium T shirt</h3>
                <p><strong>Rs. 199</strong></p>
                <div><StarOutlineOutlined className='text-yellow-400' /><StarOutlineOutlined className='text-yellow-400' /><StarOutlineOutlined className='text-yellow-400' /><StarOutlineOutlined className='text-yellow-400' /><StarOutlineOutlined /></div>
            </div>
            </a>
        </div>
    )
}

export default Product