import { Menu, ShoppingCart, AccountCircle } from '@material-ui/icons'
import { Badge } from '@mui/material'
import React, { createRef, useState } from 'react'
import SideBar from './SideBar'
import $ from 'jquery';
import { findDOMNode } from 'react-dom';
const Navbar = () => {
    const [hideShow, setHideShow] = useState('-translate-x-full');
    let dropdown = createRef();

    const handleClick = () => {
        let dropdown1 = findDOMNode(dropdown.current)
        $(dropdown1).toggle();
    }
    return (
        <>
            <nav className='flex justify-between items-center px-1 xs:px-2 py-1 shadow-lg sticky top-0 z-50 bg-white bg-opacity-80'>
                <div className='sm:hidden block'>
                    <button className='sm:block ' onClick={() => {
                        if (hideShow) {
                            setHideShow(false)
                        } else {
                            setHideShow('-translate-x-full')
                        }
                    }}><Menu className='cursor-pointer primary-color ' /></button>
                </div>
                <div className={`${hideShow} transition-all absolute xs:top-[46.98px] top-[36.98px] z-50 left-0  sm:w-48`}>
                    <SideBar />
                </div>
                <div className='translate-x-4'>
                    <img className='w-12 xs:w-16 inline-block ' src='/images/logo.png' alt="logo" /> <span className=' primary-color hidden xs:inline-block text-xs'>Experience the lifestyle</span>
                </div>
                <div className='hidden sm:block'>
                <ul className='flex space-x-2 primary-color text-sm lg:text-base'>
                    <li><button>Home</button></li>
                    <div className='relative'><button onClick={handleClick}>Categories</button><div className='transition-all hidden absolute bg-zinc-100 px-1 rounded' ref={dropdown}>
                        <button>Male</button><button>Female</button>
                    </div></div>
                    <li><button>Customer service</button></li>
                    <li><button>Blog</button></li>
                    <li><button>Sign in</button></li>
                </ul></div>
                <div className='space-x-2 px-2'>
                    <AccountCircle className='cursor-pointer primary-color' />
                    <Badge badgeContent={4} color="primary">
                        <ShoppingCart className='cursor-pointer primary-color' />
                    </Badge>
                </div>
            </nav>
        </>
    )
}

export default Navbar