import React from 'react';
import aos from 'aos';
import 'aos/dist/aos.css';

const NewsLetter = () => {
  aos.init();
  return (
    <div className='my-7' 
    data-aos="flip-up"
    data-aos-offset="200"
    data-aos-delay="50"
    data-aos-duration="1000"
    data-aos-easing="ease-in-out"
    // data-aos-anchor-placement="top-bottom"
    >
        <h1 className='text-3xl text-center primary-color my-4'>Get Update of new Products</h1>
        <div className='flex justify-center items-center flex-col'>
        <p className='xs:text-sm w-5/6 text-xs text-center'>Lorem ipsum, dolor sit amet consectetur adipisicing elit. Est repudiandae qui, eligendi sunt explicabo nobis?</p>
        <form>
            <div className='bg-yellow-400 border-2 border-yellow-400 my-3'>
            <input className='p-1 xs:w-72 outline-none' type="email" name="newsletter"placeholder='Please enter your email here.' /><button className='px-3 text-white'>Submit</button>
            </div>
        </form>
        </div>
    </div>
  )
}

export default NewsLetter