import React from 'react';
const Signup = () => {
  return (
    <div id='Signup-container'  className='bg-indigo-600 h-screen w-screen flex items-center justify-center flex-col'>
      <div className='flex items-center justify-center flex-col bg-white px-4 rounded-lg pb-2'>
        <img className='w-28 my-6' src='/images/logo.png' alt="" />
<form className='flex flex-col space-y-1'>
    <input className='border-2 rounded-sm px-2 border-yellow-400 ' type="text" placeholder='Username'/><input className='border-2 rounded-sm px-2 border-yellow-400 ' type="text" placeholder='email'/>
    <input className='border-2 rounded-sm px-2 border-yellow-400 ' type="password" name="" placeholder='Password'/><input className='border-2 rounded-sm px-2 border-yellow-400 ' type="password" name="" placeholder='Confirm Password'/>
    <button className='px-4 rounded-sm bg-yellow-400 text-white' type="submit">Signup</button>
    <p className='text-zinc-500 text-sm'>Already have an account? <a href="/" className='text-yellow-500'>Login</a></p>
</form>
</div>
    </div>
  )
}

export default Signup