import React from 'react';
import NewsLetter from '../components/NewsLetter'
import Footer from '../components/Footer'
import Navbar from '../components/Navbar';
const Product = () => {
    return (
        <>
        <Navbar/>
        <div className='flex px-1 xs:px-3 flex-wrap xs:flex-nowrap'>
            <img className='w-60 sm:w-80 h-auto' src='/images/shirt.jpg' alt="" />
            <div className='xs:mt-32 my-2'>
                <h1 className='text-2xl primary-color'>Product Name </h1>
                <p className='text-sm md:w-4/6'>Lorem ipsum dolor sit amet consectetur, adipisicing elit. Pariatur inventore aliquam quod laboriosam, perspiciatis ducimus nostrum voluptatem ex obcaecati in! Amet facere sequi fuga dicta</p>
                <strong>$20</strong>
                <ul className='flex space-x-3'>
                    <li className='flex items-center space-x-2 '>Color: <div className='bg-yellow-400 rounded-lg w-4 h-4'></div><div className='bg-black rounded-lg w-4 h-4'></div><div className='bg-red-600 rounded-lg w-4 h-4'></div></li>
                    <li>Size: <select className='border border-yellow-400' name="" id=""><option value="">sm</option>
                    <option value="">md</option>
                    <option value="">lg</option></select></li>
                </ul>
                <div>
                    <button className=' text-4xl px-3'>-</button><span className='border-2 border-yellow-400 px-2 rounded'>1</span><button className=' text-2xl px-3'>+</button>
                </div>
                <button className='border-2 border-yellow-400 my-2 px-3'>Add to Cart</button>
            </div>
        </div>
        <NewsLetter/>
        <Footer/>
        </>
    )
}

export default Product