import React from 'react'
import Footer from '../components/Footer'
import Navbar from '../components/Navbar'
import NewsLetter from '../components/NewsLetter'

const Message = () => {
    let messages= true
  return (
    <div>
        <Navbar/>
       {!messages && <div>
<p className='text-center primary-color text-lg my-32'>No new message for you</p>
</div>} 
<div className='border px-3 py-1 mx-1 my-3'>
    <p className='primary-color '>Your order is shipped. Please Submit your review.</p>
    <p className='text-xs'>1 day ago.</p>
        </div>
        <NewsLetter/>
        <Footer/>
    </div>
  )
}

export default Message