import React from 'react'
import aos from 'aos';
import 'aos/dist/aos.css';

const CategorySection = () => {
  aos.init();
  return (
    <div >
      <h1 className='text-center my-6 text-3xl text-yellow-500'>Product Categories</h1>
      <div className='flex flex-wrap items-center justify-center space-x-2 space-y-2'>

        <div className='relative w-1/4 bg-slate-300 overflow-hidden'
          data-aos="flip-up"
          data-aos-offset="150"
          data-aos-delay="50"
          data-aos-duration="1000"
          data-aos-easing="ease-in-out">
          <a href="/">
            <img className='hover:scale-110 transition-all' src='/images/shoe.jpg' alt="pant_photo" />
            <h1 className='absolute bg-[#f2ae3bcf] bottom-28 w-full text-center text-white text-xl'>For Men</h1>
          </a>
        </div>
        <div className='relative w-1/4 bg-slate-300 overflow-hidden mx-4'
          data-aos="flip-up"
          data-aos-offset="150"
          data-aos-delay="50"
          data-aos-duration="1000"
          data-aos-easing="ease-in-out">
          <a href="/">
            <img className='hover:scale-110 transition-all' src='/images/womanShoe.jpg' alt="pant_photo" />
            <h1 className='absolute bg-[#f2ae3bcf] bottom-28 w-full text-center text-white text-xl'>For Woman</h1>
          </a>
        </div>
      </div>
    </div>
  )
}

export default CategorySection